import React from 'react';
import { If } from 'react-statements'

class IfExample extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isShow: true,
      isNum: 1,
      isNumZero: 0,
    };
  }
  handleIf = () => {
    this.setState({isShow: !this.state.isShow});
  }
  render() {
    const { isShow, isNum, isNumZero } = this.state;

    return (
      <div>
      <fieldset>
        <legend>If </legend>
        <div>
          <button onClick={this.handleIf}>
            {!isShow ? '' : ''}
          </button>
          <If when={isShow}>
            <p></p>
          </If>
        </div>
        <div>
          <If when={isNum} children={<p>number1</p>} />
          <If when={isNumZero} children={<p>number, 1</p>} />
        </div>
      </fieldset>
    </div>
    );
  }
}

export default IfExample;



